<template>
  <div :class="['card', card_color]" v-cloak>
    <img :src="`images/${card_color}.jpg`" class="card-img-top" alt="image">
    <div class="clearfix">
      <div class="spinner-grow float-end" role="status">
        <span :class="['visually', card_color]">{{ numero_carte }}</span>
      </div>
    </div>
    <div class="card-body">
      <h5 class="card-title">{{ card_titre }}</h5>
      <p class="card-text">{{ question['Énoncé']}}</p>
      <img class="question" v-if="question['Visuel_question'] != ''"
        :src="`https://math-qcm.go.yj.fr/upload/images/${question['Visuel_question']}`" />
      <form>
        <hr>
        <template v-for="indice in indice_question" :key="indice">
          <div class="form-check"
            v-if="parseInt(question['Propositions'][indice]['État_proposition']) || !isVisible_propositions">
            <input class="form-check-input" type="radio" name="flexRadioDefault" :id="`flexRadioDefault+${indice}`">
            <label :class="['form-check-label']" :for="`flexRadioDefault+${indice}`">
              {{ question['Propositions'][indice]['Proposition'] }}
            </label>
            <img v-if="question['Propositions'][indice]['Visuel_proposition'] != 'Null'"
              :src="`https://math-qcm.go.yj.fr/upload/images/${question['Propositions'][indice]['Visuel_proposition']}`" />
            <hr>
          </div>

        </template>
        <!--hr-->
      </form>
      <a href="#" class="btn btn-success valide" @click="isVisible_propositions = !isVisible_propositions"
        v-if="!isVisible_propositions && isVisible_afficher_proposition_correcte">Cacher les fausses propositions</a>
      <h4>Tirer une carte</h4>
      <template v-for="couleur in colors" :key="couleur">
        <a href="#" :class="['btn', couleur, 'cat']" @click="color(couleur)"></a>
      </template>
      <div class="mt-3">
        <label for="carteAdverse" class="form-label">Voir ma carte</label>
        <input type="text" class="form-control" id="carteAdverse" v-model="numero_carte_adverse"
          placeholder="Le numéro de la carte de l'adversaire">
      </div>
      <a href="#" class="btn btn-success valide adverse" @click="afficher_carte(numero_carte_adverse)">Afficher la carte
        {{ numero_carte_adverse }}</a>
    </div>
  </div>

</template>

<script>

export default {
  name: 'Card_jeu',
  data() {
    return {
      card_color: 'jaune',
      card_titre: 'Arithmétique et Calculs',
      question: this.data['75'],
      indice_question: [0, 1, 2, 3],
      colors: ['jaune', 'bleu', 'vert', 'rose', 'rouge'],
      numero_carte_adverse: '',
      numero_carte: '75',
      isVisible_propositions: false,
      isVisible_afficher_proposition_correcte: true
    }
  },
  props: {
    cathegorie: {
      type: Object
    },
    data: {
      type: Object
    }
  },

  methods: {
    afficher_carte(value) {
      let i = 0
      for (let key in this.cathegorie) {
        if (this.cathegorie[key].includes(value)) {
          this.question = this.data[value]
          this.card_titre = key
          this.card_color = this.colors[i]
          this.numero_carte = value
          this.isVisible_afficher_proposition_correcte = false
          break
        }
        i++
      }
    },
    color(value) {
      this.card_color = value

      switch (value) {
        case 'jaune':
          this.card_titre = "Arithmétique et Calculs"
          break;
        case 'bleu':
          this.card_titre = "Les Fonctions"
          break;
        case 'vert':
          this.card_titre = "Géométrie"
          break;
        case 'rose':
          this.card_titre = "Statistiques et Probabilités"
          break;
        case 'rouge':
          this.card_titre = "Algèbre"
          break;
      }
      this.numero_carte = this.cathegorie[this.card_titre][this.aleatoire(this.cathegorie[this.card_titre].length)]
      this.question = this.data[this.numero_carte]
      this.isVisible_propositions = false
      this.numero_carte_adverse = ''
      this.isVisible_afficher_proposition_correcte = true
    },
    aleatoire(taille_liste) {
      return Math.floor(Math.random() * taille_liste)
    },
    latex() {
      window.MathJax.typesetPromise().then(() => {
        window.MathJax.typeset();
      }).catch(function (err) {
        return console.log(err.message);
      });
    }
  },
  mounted() {
    this.latex()
  },
  afterMount() {
    this.latex()
  },
  updated() {
    this.latex()
  }
}
</script>

<style scoped>
.card-title {
  font-weight: bold;
  font-family: titre;
  font-size: 26px;
  margin-bottom: 20px;
}

#carteAdverse {
  text-align: center;
}

.adverse {
  margin-top: 10px;
}

.question {
  margin-bottom: 10px;
}

.spinner-grow {
  width: 50px;
  height: 50px;
  padding-top: 10px;
}

label[for="carteAdverse"] {
  font-family: titre;
  font-size: 20px;
}

img {
  margin: 0;
  padding: 0;
  width: 100%;
}

form {
  margin-bottom: 30px;
}

.form-check {
  margin: 0;
  padding: 0;
}

h4 {
  display: block;
  margin-top: 50px;
  font-family: titre;
  font-size: 20px;
  font-weight: bold;
}

.cat {
  width: 50px !important;
  height: 50px !important;
  margin-left: 5px;
  border-style: groove !important;
  border-color: white !important;
  box-shadow: 2px 2px 2px grey;
}

.card {
  width: 25rem !important;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
  border: none;
  margin-top: 20px;
  font-weight: bold;
}

input[type='radio'] {
  display: none;
}

.jaune {
  background-color: rgba(216, 216, 0, 1);
  color: black;
}

.vert {
  background-color: rgba(0, 255, 0, 1);
  color: black;
}

.rouge {
  background-color: rgba(255, 0, 0, 0.8);
}

.rose {
  background-color: rgba(243.6, 0, 255, 1);
}

[v-cloak] {
  display: none;
}

.bleu {
  background-color: rgba(0, 0, 255, 1);
}

.btn {
  margin-bottom: 10px;
  color: white;
  font-weight: bold;
  text-shadow: 2px 2px 2px grey;
}

label {
  margin: 0;
  padding: 0;
}

.valide {
  display: block;
  background-color: beige;
  color: black;
  font-family: titre;
  font-size: 20px;
  text-shadow: 2px 2px 2px grey;
}

.visually {
  text-align: center;
  font-size: 20px;
}

@font-face {
  font-family: titre;
  src: url(../../public/fonts/Julee-Regular.ttf)
}

@media screen and (max-width: 500px) {
  .card {
    width: 100% !important;
    margin: 0;
    margin-top: 0;
  }
}
</style>