<template>
  <Card_jeu v-if="data != null" :cathegorie="cathegorie" :data="data" />
</template>

<script>

import Card_jeu from './components/Card.vue'

export default {
  name: 'App',
  components: {
    Card_jeu
  },
  data() {
    return {
      data: null,
      cathegorie: { "Arithmétique et Calculs": [], "Les Fonctions": [], "Géométrie": [], "Statistiques et Probabilités": [], "Algèbre": [] },
    }
  },
  methods: {
    creer_cathegorie(data) {
      for (let key in data) {
        if (!isNaN(parseInt(key))) {
          if (data[key]['Chapitre'] == "Ensemble de nombre" || data[key]['Chapitre'] == "Arithmétique") {
            this.cathegorie["Arithmétique et Calculs"].push(key)
          } else if (data[key]['Chapitre'] == "Fonctions de référence" || data[key]['Chapitre'] == "Étude des variations d'une fonction" || data[key]['Chapitre'] == "Étude graphique et algébrique de fonctions") {
            this.cathegorie["Les Fonctions"].push(key)
          } else if (data[key]['Chapitre'] == "Géométrie dans le plan" || data[key]['Chapitre'] == "Vecteurs" || data[key]['Chapitre'] == "Positions relatives de droites") {
            this.cathegorie["Géométrie"].push(key)
          } else if (data[key]['Chapitre'] == "Information chiffrée" || data[key]['Chapitre'] == "Probabilités et échantillonnage" || data[key]['Chapitre'] == "Statistique descriptive") {
            this.cathegorie["Statistiques et Probabilités"].push(key)
          } else {
            this.cathegorie["Algèbre"].push(key)
          }
        }
      }
      console.log(this.cathegorie)
    },
  },
  beforeMount() {
    window.axios
      .get('https://math-qcm.go.yj.fr/api_math/lire.php')
      .then(response => (this.data = response['data']))
      .then(data => this.creer_cathegorie(data))
  },
}
</script>

